.navigation {
  height: 45px;
  top: 0px;
  position: sticky;
  text-align: center;
  align-items: center;  
  z-index: 10;
}

.menu {
  min-width: 900px;
  list-style: none;
  position: relative;
  overflow: hidden;
}
.menu li {
  display: inline-block;
  width: 15.5%;
  border: 1px; 
  margin: 0px;
  height: 45px;
}
