.pumpAnimated, .pumpBody, .pumpAnimatedCable {
  position: relative;
  right: 5px;
}

.pumpAnimated, .pumpAnimatedCable {
  animation-iteration-count: infinite;
  animation-duration: 5s;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-duration: 5s;
  -webkit-animation-timing-function: linear;
  -o-animation-iteration-count: infinite;
  -o-animation-duration: 5s;
  -o-animation-timing-function: linear;
  -moz-animation-iteration-count: infinite;
  -moz-animation-duration: 5s;
  -moz-animation-timing-function: linear;



  /* animation-direction: alternate; */
}
.pumpBody {
  z-index: 1;
  bottom: 142px;
}
.pumpAnimated {
  animation-name: rotation;
  -webkit-animation-name: rotation;
  -o-animation-name: rotation;
  -moz-animation-name: rotation;
  z-index: 5;
}

.pumpAnimatedCable {
  animation-name: move;
  -webkit-animation-name: move;
  -o-animation-name: move;
  -moz-animation-name: move;
  z-index: 4;
  top: 140px;
}

@keyframes rotation {
  from, to {
    transform: rotate(0deg) translate(0px, 0px);
  }
  50% {
    transform: rotate(20deg) translate(-7px, -5px);
  }
}

@keyframes move {
  from, to {
    transform: translateY(-1px) rotate(0deg) translateX(-1px);
  }
  25% {
    transform: rotate(5deg) translateX(-1px);
  }
  50% {
    transform: translateY(1px) translateX(-1px) rotate(10deg);
  }
  75% {
    transform: rotate(5deg) translateX(-1px);
  }
}