.contacts {
  text-align: center;
  align-items: center;
}
.firstSec, .secondSec {
  width: 40%;
  border: 1px;
  overflow: hidden;
  min-width: 475px;
}
.firstSec {
  display: inline-block;
  margin: 40px 0px
}

.secondSec {
  display: inline-block;
  position: relative;
  bottom: 40px;
}

.contactsComp {
  text-align: left;
  width: 475px;
  height: 317px;
  background: #161515;
  filter: drop-shadow(0px 13px 17px rgba(0, 0, 0, 0.25));
  margin: 0px auto 27px;
}

.contacts-icon {
  position: relative;
  top: 18px;  
  left: 30px;
}

.mapComp {
  position: relative;
  border: 1px solid black;
  width: 475px; 
  height: 302px;
  filter: drop-shadow(0px 13px 17px rgba(0, 0, 0, 0.25));
  margin: 20px auto 0px;
  overflow: hidden;
}

.mapComp._hidden, .mapComp._hidden1 {
  z-index: 10;
  position: absolute;
  visibility: hidden;
}

.feedbackComp {
  text-align: left;
  width: 475px;
  
  height: 646px;
  background-color: #131212;
  filter: drop-shadow(0px 13px 17px rgba(0, 0, 0, 0.25));
  margin: 0px auto 0px;
}

.feedbackComp textarea {
  background: #FFFCFC;
  border: 1px solid #000000;
  box-sizing: border-box;
  width: 409px;
  height: 141px;
  position: relative;
  left: 32px;
  bottom: 7px;
  outline: none;
  resize: none;
  overflow: hidden;
}
.feedbackComp input {
  background: #FFFCFC;
  border: 1px solid black;
  box-sizing: border-box;
  outline: none;
  width: 40%;
  float: left;
}
.feedbackComp button {
  background:#D52323;
  border-radius: 11px;
  width: 212px;
  height: 39px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 113.69%;
  color: #FFFDFD;
  position: relative;
  left: 32px;
}
@media (max-width: 1300px) {
  .firstSec, .secondSec {
    width: 45%;
  }
}
@media (max-width: 1100px) {
  .firstSec, .secondSec {
    width: 50%;
  }
}
@media (max-width: 1000px) {
  .contacts {
    text-align: center;
    align-items: center;
  }
  .firstSec, .secondSec {
    overflow: hidden;
    min-width: 475px;
    width: 100vw;
  }
  .firstSec {
    display: block;
    margin: 40px auto 0px;
  }
  
  .secondSec {
    display: block;
    position: relative;
    bottom: 40px;
    margin: 80px auto 0px;
  }
  
  .contactsComp {
    text-align: left;
    width: 80vw;
    height: 317px;
    margin: 0px auto 27px;
  }
  
  .contacts-icon {
    position: relative;
    top: 18px;  
    left: 30px;
  }
  
  .mapComp {
    z-index: 10;
    position: absolute;
    visibility: hidden;
  }
  
  .mapComp._hidden {
    visibility: visible;
    position: relative;
    right: 1px;
    bottom: 1px;  
    width: 80vw; 
    height: 302px;
    filter: drop-shadow(0px 13px 17px rgba(0, 0, 0, 0.25));
  }

  .feedbackComp {
    text-align: left;
    width: 80vw;
    height: 646px;
    margin: 0px auto 0px;
  }
  
  .feedbackComp textarea {
    width: 90%;
    height: 141px;
    position: relative;
    left: 32px;
    bottom: 7px;
  }
  
  .feedbackComp button {
    width: 212px;
    height: 39px;
    position: relative;
    left: 32px;
  }
}
@media (max-width: 750px) {
  .firstSec, .secondSec {
    width: 100%;
  }
  .contactsComp {
    width: 100vw;
    margin: 0px auto 35px;
  }
  .mapComp._hidden {
    visibility: hidden;
    position: absolute;
  }
  .mapComp._hidden1 {
    width: 100vw;
    margin: 0px auto 0px;
    visibility: visible;
    position: relative;
    right: 1px;
    bottom: 1px;  
    height: 302px;
  }
  .feedbackComp {
    width: 100vw;
    margin: 0px auto 0px;
  }
  .feedbackComp textarea {
    width: 90vw;
  }
}
@media (max-width: 450px) {
  .feedbackComp textarea {
    width: 80vw;
  }
}