/*
Формула авто-масштабирования: 
calc([min size]px + ([max size] — [min size]) * ((100vw — 500px) / (2560 — 500)))
Header, MainContainer и Basement width:
calc(900px + (1920 - 900) * ((100vw - 900px) / (1920 - 900)))
*/

*,
*::after,
*::before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
@font-face {
  font-family: "Sansation Light";
  font-style: normal;
  font-weight: 300;
  /* Браузер сначала попробует найти шрифт локально */
  src: local("Sansation Light"),
       /* Если не получилось, загрузит woff2 */
       url("./fonts/SansationLight.eot") format("emdedded-opentype"),
       /* Если браузер не поддерживает woff2, загрузит woff */
       url("./fonts/SansationLight.woff") format("woff"),
       url("./fonts/SansationLight.ttf") format("truetype");
}

body {
  background-color: white;
  margin: 0px;
  min-width: 900px
}
h1 {
  font-family: Sansation Light;
  font-style: normal;
  font-weight: 300;
  font-size: 28px;
  line-height: 31px;
  color: #FFFFFF;
}
h2 {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size:  calc(22px + (38 - 22) * (100vw - 400px) / (1800 - 400));
  text-align: center;
  color: #C2B5B5;
  margin: 0px;
}
h4 {
  font-family: Sansation Light;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 31px;
  color: #FFFFFF;
}
h5 {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 113.69%;
  color: #FDF7F7;
}
.contactsComp h5 {
  padding: 19px 12px 0px 30px;
}
.feedbackComp h5 {
  padding: 32px 12px 0px 32px;
}
h6 {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 113.69%;
  color: #FFFDFD;
  
}
.contactsComp h6 {
  padding: 19px 12px 28px 69px;
}
.feedbackComp h6 {
  padding: 33px 12px 12px 33px;
}
.mainContainer {
  padding-top: calc(40px + (60 - 40) * ((100vw - 900px) / (1920 - 900)));
}
._mainBackground {
  background: url(../img/Background2/mainBackground.png) no-repeat;
}
._addBackground{
  background: url(../img/Background2/addBackground.png)  no-repeat;
}
._mainBackground, ._addBackground {
  background-blend-mode: lighten;
  background-size: cover;
  background-position: left;
}
.anchor:hover {
  cursor: pointer;
  color: black;
}

.main, .about, .catalog, .services, .contacts {
  position: relative;
  min-height: 900px;
}

